section {
  width: 1200px;
  position: relative;
  margin: 0 auto;
  padding: 8vh 20px;
  .title {
    font-size: 2rem;
    font-weight: 600;
    padding-bottom: 0.5rem;
    margin-bottom: 0.5em;
    text-align: left;
    position: relative;
    border-bottom: 1px solid #333;
    &:before {
      width: 5px;
      height: 15px;
      right: 0px;
      background: $point-color;
      position: absolute;
      content: "";
      bottom: 0;
      transition: all linear 1s;
      border-radius: 5px;
    }
  }
}

@include labtop {
  section {
    width: 100%;
  }
}

.section--intro {
  padding-top: 20vh;
  display: flex;
  article {
    position: relative;
    width: 50%;
  }
  article.intro-text {
    color: #000;
    h1 {
      width: 100%;
      z-index: 1;
      font-size: 2rem;
      line-height: 1.3em;
      font-weight: 400;
      span {
        display: inline-block;
      }
    }
    .text-in-out-items {
      display: inline-block;
      height: 1em;
      font-weight: 600;
      span {
        position: absolute;
        opacity: 0;
        transition: all 0.5s;
        &.active {
          opacity: 1;
        }
      }
    }
  }
  article.intro-img {
    text-align: center;
    img {
      height: 300px;
    }
  }
}

.section--skills {
  .skill-type {
    margin: 2rem 0;
    h3 {
      margin-bottom: 1.5rem;
      color: #777;
    }
    .skill-icons {
      margin: 2rem 0;
      padding-bottom: 1.5rem;
      border-bottom: 1px solid #eee;
    }
    .skill-icons img {
      width: 64px;
      margin-right: 2.2vw;
    }
  }
}

.section--timeline {
  .timeline {
    width: 90%;
    margin-left: 10%;
    position: relative;
    &:before {
      position: absolute;
      border-left: 4px solid #eee;
      content: "";
      width: 70%;
      height: 80%;
      margin-left: -6.5%;
      z-index: -1;
      top: 10%;
    }
  }
  .item {
    position: relative;
    padding: 2rem;
    border: 2px solid #eee;
    border-radius: 0.3rem;
    margin: 3rem 0;
    &:before {
      position: absolute;
      width: 0px;
      height: 0px;
      border: 15px solid transparent;
      border-right-color: #f1f1f1;
      content: "";
      right: 100%;
      top: calc(50% - 15px);
    }
    &:after {
      position: absolute;
      content: "";
      right: 100%;
      border: 11px solid transparent;
      border-right-color: #fff;
      top: calc(50% - 11px);
    }
    .date {
      position: absolute;
      width: 72px;
      height: 72px;
      padding: 0.5rem;
      background-color: #fff;
      left: -10%;
      top: calc(50% - 32px);
      text-align: center;
      border-radius: 50%;
      span {
        border-radius: 50%;
        font-weight: 200;
        line-height: 58px;
        color: #fff;
        background-color: #bbb;
        width: 100%;
        height: 100%;
        display: block;
        position: relative;
        z-index: 1;
      }
      &.now {
        span {
          background-color: $point-color;
        }
        &:after {
          content: "";
          border-radius: 50%;
          height: 100%;
          width: 100%;
          top: 0;
          left: 0;
          position: absolute;
          background-color: #f1f1f1;
        }
      }
    }
    h3 {
      margin-bottom: 0.3rem;
      span {
        font-weight: 200;
        font-size: 0.8rem;
        color: #bbb;
      }
    }
    h5 {
      margin-bottom: 1rem;
      color: #999;
      font-weight: 200;
    }
  }
}

.section--project {
  article {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    position: relative;
  }
  article::after {
    content: "";
    max-width: calc(33% - 1rem);
    flex: auto;
  }
  .project--wrapper {
    width: calc(33.3% - 1rem);
    height: 280px;
    float: left;
    margin-bottom: 1.5rem;
    position: relative;
    background-size: 100%;
    background-repeat: no-repeat;
    background-position: center;
    border-radius: 0.35rem;
    overflow: hidden;
    cursor: pointer;
    transition: all 0.325s cubic-bezier(0.1, 0.05, 0.075, 0.1);
    .project {
      width: 100%;
      height: 100%;
      opacity: 0;
      padding: 1.5rem;
      border-radius: inherit;
      position: absolute;
      color: #fff;
      overflow: hidden;
      transition: all 0.3s ease-in-out;
      transform: translateX(-50%);
      h2 {
        font-size: 1.2rem;
        margin-bottom: 0.5rem;
      }
      p {
        color: #ddd;
        font-weight: 200;
      }
      a {
        width: calc(33.3% - 10px);
        text-align: center;
        float: left;
        position: relative;
        margin-top: 50px;
        background: #000;
        border-radius: 0.25rem;
        padding: 1rem 0;
        margin-right: 10px;
        opacity: 0.8;
        font-style: unset;
        &:hover {
          opacity: 1;
        }
      }
    }
    &:before {
      position: absolute;
      content: "";
      width: 100%;
      height: 100%;
      background-color: #000;
      opacity: 0.1;
      pointer-events: none;
      transition: opacity 0.3s cubic-bezier(0.1, 0.1, 0.05, 0.05);
      border-radius: 0.35rem;
    }
    &:hover {
      background-size: 103%;
      &:before {
        opacity: 0.75;
      }
      .project {
        opacity: 1;
        transform: translateX(0);
      }
    }
  }
}

aside {
  position: fixed;
  top: 20vh;
  right: 5vw;
  height: 30vh;
  padding: 20px;
  width: 100px;
  .sun {
    opacity: 1;
  }
  .moon {
    opacity: 0;
    display: none;
  }
}
.sun {
  // position: relative;
  position: absolute;
  width: 60px;
  height: 60px;
  border-radius: 50%;
  background-color: #f2b826;
  top: 0;
  transition: opacity 0.2s linear;
  &:before {
    position: absolute;
    content: "";
    width: 36px;
    height: 36px;
    left: 12px;
    top: 12px;
    border-radius: 50%;
    background-color: #f28923;
    z-index: 1;
  }
  .spear {
    position: absolute;
    background-color: #f5d266;
    width: 16px;
    height: 16px;
    border-radius: 30% 15% 30% 50%;
    &:nth-child(1) {
      top: -26px;
      left: 22px;
      transform: rotate(-45deg);
    }
    &:nth-child(2) {
      top: -10px;
      left: 56px;
    }
    &:nth-child(3) {
      top: 22px;
      left: 70px;
      transform: rotate(45deg);
    }
    &:nth-child(4) {
      top: 54px;
      left: 56px;
      transform: rotate(90deg);
    }
    &:nth-child(5) {
      top: 70px;
      left: 22px;
      transform: rotate(135deg);
    }
    &:nth-child(6) {
      top: 54px;
      left: -10px;
      transform: rotate(180deg);
    }
    &:nth-child(7) {
      top: 22px;
      left: -26px;
      transform: rotate(225deg);
    }
    &:nth-child(8) {
      top: -10px;
      left: -10px;
      transform: rotate(270deg);
    }
  }
}
.moon {
  // position: relative;
  position: absolute;
  width: 60px;
  height: 60px;
  border-radius: 50%;
  background-color: #ffcc00;
  bottom: 0;
  &:before {
    position: absolute;
    content: "";
    width: 60px;
    height: 60px;
    background: #fff;
    left: 20px;
    top: 20px;
    border-radius: 50%;
  }
  .star {
    position: absolute;
    left: 40px;
    top: 60px;
    .angle {
      border: solid transparent;
      border-top: 6px solid #ffcc00;
      border-left-width: 9px;
      border-right-width: 9px;
      border-bottom: 0;
      position: absolute;
      border-radius: 8px;
      &:nth-child(1) {
        transform: rotate(180deg);
      }
      &:nth-child(2) {
        transform: rotate(105deg);
      }
      &:nth-child(3) {
        transform: rotate(255deg);
      }
    }
  }
}
