.dark--img {
  display: none;
}
body {
  transition-property: color, background-color, border-color;
  transition-duration: 0.5s;
  transition-timing-function: cubic-bezier(0.05, 0.05, 0.1, 0.3);
}
body.dark {
  background-color: #222;
  color: #eee;
  * {
    color: #eee;
  }
  nav ul {
    * {
      background-color: #000;
    }
  }
  .dark--img {
    display: block;
  }
  .day--img {
    display: none;
  }
  section {
    .title {
      border-color: #ccc;
      &:before {
        background: #fff;
      }
    }
  }
  .section--project .project--wrapper {
    &:hover {
      background: #222 !important;
    }
    .project a {
      background: #fff;
      color: #000;
    }
  }
  .section--timeline {
    .item {
      &:after {
        border-right-color: #222;
      }
      .date {
        background-color: #222;
      }
      .date:not(.now) span {
        background-color: #666;
      }
    }
  }
  .section--skills .skill-type {
    h3 {
      color: #bbb;
    }
    .skill-icons {
      img {
        background: #f2f2f2;
        border-radius: 8%;
        padding: 0.25rem;
      }
    }
  }
  aside {
    background-color: transparent;
    transform: rotate(180deg);
    .sun {
      opacity: 0;
      display: none;
    }
    .moon {
      opacity: 1;
      display: block;
      background-color: #ffcc00;
      &:before {
        background-color: #222;
      }
    }
  }
}
